ul{
    display: inline-block;
    list-style-type: none;
    width: 100%;
    margin: 0;
    padding: 0;
    font-weight: 700;
}

ul li{
    padding: 0 0 0 5px;
    text-align: left;
    min-height: 2rem;
    line-height: 2rem;
    transition: 250ms all;
}

ul li:hover{
    background-color: grey;
    color: #fff;
    cursor: pointer;
}

